  async ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('userLogin');
    this.userLoginDetails = this.common.getValueFromSessionStorage('userLoginInfo');
    this.reactiveFormName = this.formBuilder.group({
      searchText: ['']
    });
    this.initMasterData();
    this.dtOptions = {
      order: [[0, 'desc']],
      language: ({
        "zeroRecords": await this.translate.get('NoDataAvailable').toPromise(),
        "search": await this.translate.get('Search').toPromise(),
        "info": await this.translate.get('Information').toPromise(),
        "infoFiltered": await this.translate.get('infoFiltered').toPromise(),
        "infoEmpty": await this.translate.get('infoEmpty').toPromise(),
        "lengthMenu": await this.translate.get('lengthMenu').toPromise(),
        "paginate": {
          "first": await this.translate.get('First').toPromise(),
          "last": await this.translate.get('Last').toPromise(),
          "next": await this.translate.get('Next').toPromise(),
          "previous": await this.translate.get('Previous').toPromise(),
        }
      }),
    }

  }

  // Used for call All APIs
  async initMasterData() {
    let selectedCompanyInfo = this.common.getValueFromSessionStorage('selectedCompanyInfo');
    if (selectedCompanyInfo != null) {
      await this.getDriversList();
      await this.getListCompanyDriverStagingList();
    }
    //this.showLicenseNumberValidation();
    //this.addDriverValidation();
    await this.onChangeOfCompany();
    this.showDriverVehicleAddBtn(selectedCompanyInfo);
    this.apiCall.spinner.hide();
  }

  public subscription: Subscription;
  public ngOnDestroy(): void {
    // onDestroy cancels the subscribe request
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
    // Do not forget to unsubscribe the event
    if (this.dtTrigger) {
      this.dtTrigger.unsubscribe();
    }
  }

  //on changing the company name
  async onChangeOfCompany() {
    //this.apiCall.spinner.show();
    this.subscription = this.common.getContent().subscribe(async msg => {
      let selectedCompanyInfo = this.common.getValueFromSessionStorage('selectedCompanyInfo');
      if (selectedCompanyInfo != null) {
        this.apiCall.spinner.show();
        this.common.mesage = {};
        this.selectedCompanyInfo = selectedCompanyInfo;
        //this.showLicenseNumberValidation();
        //this.addDriverValidation();
        this.showDriverVehicleAddBtn(selectedCompanyInfo);
        await this.getDriversList();
        await this.getListCompanyDriverStagingList();
        this.apiCall.spinner.hide();
      }
    });
  }

